
export function enumString(enumeration, enumProperty) {
  const enumName = enumeration[enumProperty];
  if (!enumName) {
    console.warn(
      `${enumProperty} is not a valid value for provided enumeration`,
      JSON.stringify(enumeration).substr(0, 100)
    );
    return '';
  }
  return enumName.replace(/([A-Z])/g, ' $1').slice(1);
}

export function enumValues(enumeration) {
  const arr = [];
  for (const value in enumeration) {
    if (isNaN(Number(value))) {
      arr.push(enumeration[value]);
    }
  }
  return arr;
}

export function enumKeys(enumeration) {
  return Object.keys(enumeration);
}

export function enumKeyValue(enumeration, enumValue) {
  let keys = Object.keys(enumeration).filter(x => enumeration[x] == enumValue);
  return keys.length > 0 ? keys[0] : null;
}
