import { Component, OnDestroy, OnInit } from '@angular/core';
import { CheckListsService } from '../services/check-lists/check-lists.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import {
  CheckListStatus,
  CheckList
} from '../services/check-lists/check-lists.types';
import { Router } from '@angular/router';
import {
  AuthenticationService,
  CheckListPermissions,
  PermissionGroup,
  SearchFilterService
} from '../services';
import { debounceTime, filter } from 'rxjs/operators';
import { CheckListsWizard } from './check-lists.wizard';

@Component({
  selector: 'app-check-lists',
  templateUrl: './check-lists.component.html',
  styleUrls: ['./check-lists.component.scss']
})
export class CheckListsComponent implements OnInit, OnDestroy {
  public searchForm: FormGroup;
  public isSearching: boolean;
  public checkLists: CheckList[];
  public CheckListStatus = CheckListStatus;
  public canCreateCheckList: boolean = false;
  public shouldGetNextCheckList = true;
  public form: FormGroup;
  public offset = 0;
  public take = 10;
  public checkListFilterCache: boolean;
  public canUserEditCheckList: boolean;
  public isGettingUserRights = false;

  constructor(
    private formBuilder: FormBuilder,
    private authenticationService: AuthenticationService,
    private checkListsService: CheckListsService,
    private router: Router,
    private searchFilterService: SearchFilterService,
    private checkListsWizard: CheckListsWizard
  ) {}

  public ngOnInit() {
    this.setPermissionBooleans();
    this.searchForm = this.formBuilder.group({
      text: this.searchFilterService.searchCLTextValue
        ? this.searchFilterService.searchCLTextValue
        : ''
    });
    this.checkListFilterCache = this.searchFilterService.searchCLFilterValue;
    this.searchFilterService.searchCLFilterValueCache = this.checkListFilterCache;
    this.searchForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length >= 3 || textValue.length === 0)
      )
      .subscribe(() => {
        this.initiateFreshSearchSettings();
        this.getCheckList();
        this.searchFilterService.searchCLTextValueCache = this.searchForm.controls[
          'text'
        ].value;
      });

    this.initiateFreshSearchSettings();
    this.getCheckList();
  }

  public ngOnDestroy(): void {
    this.searchFilterService.clearCLFilter();
  }

  public initiateFreshSearchSettings(): void {
    this.checkLists = [];
    this.offset = 0;
    this.shouldGetNextCheckList = true;
  }

  public getCheckList() {
    this.isSearching = true;
    this.checkListsService
      .getCheckListsList({
        offset: this.offset,
        take: this.take,
        searchText: this.searchForm.value.text,
        includeObsolete: this.checkListFilterCache
      })
      .subscribe((checkLists: CheckList[]) => {
        this.checkLists.push(...checkLists);
        this.offset += this.take;
        if (checkLists.length < this.take) {
          this.shouldGetNextCheckList = false;
        }
        this.isSearching = false;
      });
  }

  public createCheckList() {
    this.router.navigate(['check-list', 'create-check-list']);
  }

  public handleFilterClick(includeObsolete: boolean) {
    this.checkListFilterCache = includeObsolete;
    this.searchFilterService.searchCLFilterValueCache = this.checkListFilterCache;
    this.initiateFreshSearchSettings();
    this.getCheckList();
  }

  private setPermissionBooleans() {
    this.isGettingUserRights = true;
    this.authenticationService
      .currentUserHasPermission({
        [PermissionGroup.CheckList]: [CheckListPermissions.CanCreateCheckList]
      })
      .subscribe(hasPermission => (this.canCreateCheckList = hasPermission));
    this.authenticationService
      .currentUserHasPermission({
        [PermissionGroup.CheckList]: [CheckListPermissions.CanEditCheckList]
      })
      .subscribe(hasPermission => {
        this.canUserEditCheckList = hasPermission;
        this.isGettingUserRights = false;
      });
  }

  public handleSelectedCheckListClick(checkList: CheckList) {
    this.checkListsWizard.checkLists = checkList;
    if (this.checkListsWizard.checkLists) {
      if (this.canUserEditCheckList) {
        this.router.navigate(['check-list/edit-check-list']);
      } else {
        this.router.navigate(['check-list/view-check-list']);
      }
    }
  }
}
