import { Component, OnInit } from '@angular/core';
import { CheckListsWizard } from '../check-lists.wizard';

import {
  AuthenticationService,
  CheckListStatus,
  CheckListPermissions,
  PermissionGroup,
  CheckListsService,
  CheckList,
  Toast,
  ToastService,
  ITask
} from 'src/app/services';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators
} from '@angular/forms';
import { enumValues } from 'src/utils/enumUtils';
import { HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-view-check-lists',
  templateUrl: './view-check-list.component.html',
  styleUrls: ['./view-check-list.component.scss']
})
export class ViewCheckListsComponent implements OnInit {
  public isLoading = true;
  public isGettingUserRights = false;
  public isEditable = false;
  public checkListId: string;
  public checkListNumber: number;
  public checkList: CheckList;
  public formViewCheckList: FormGroup;
  public checklistStatus = CheckListStatus;
  public checkListStatusValues: number[] = enumValues(this.checklistStatus);
  public isUpdatingCheckList = false;
  public submitted = false;
  public showSaveButton = false;
  public addedTaskList: ITask;
  public hasNoCheckListTask: Boolean;

  constructor(
    private formBuilder: FormBuilder,
    private checkListsService: CheckListsService,
    private router: Router,
    private checkListsWizard: CheckListsWizard,
    private authenticationService: AuthenticationService,
    private toastService: ToastService
  ) {
    if (this.checkListsWizard.checkLists) {
      this.checkListNumber = this.checkListsWizard.checkLists.number;
    } else {
      this.router.navigate(['check-list']);
    }
  }

  public ngOnInit(): void {
    this.viewCheckListForId();
    this.setPermissionBooleans();
  }

  public createForm() {
    const bulidCheckListDetailForm = checkList => {
      this.formViewCheckList = this.formBuilder.group({
        name: new FormControl(
          {
            value: checkList.name,
            disabled: !this.isEditable
          },
          Validators.compose([Validators.required])
        ),
        status: new FormControl(
          {
            value: checkList.status,
            disabled: !this.isEditable
          },
          Validators.compose([Validators.required])
        )
      });
    };
    bulidCheckListDetailForm(this.checkList);
    this.isLoading = false;
    this.formViewCheckList.valueChanges.subscribe(() => {
      setTimeout(() => {
        this.updateFormChanges();
      }, 1000);
    });
  }

  public updateFormChanges() {
    this.checkList.name = this.formViewCheckList.get('name').value.toString()
      ? this.formViewCheckList.get('name').value.toString()
      : '';
    this.checkList.status = parseInt(
      this.formViewCheckList.get('status').value
    );
    this.showSaveButton = true;
    this.formViewCheckList.markAsDirty();
  }

  private setPermissionBooleans() {
    this.isGettingUserRights = true;
    this.authenticationService
      .currentUserHasPermission({
        [PermissionGroup.CheckList]: [CheckListPermissions.CanEditCheckList]
      })
      .subscribe(hasPermission => {
        this.isEditable = hasPermission;
        this.isGettingUserRights = false;
      });
  }

  public viewCheckListForId(isOnInit: boolean = true) {
    this.isLoading = true;
    this.checkListsService
      .viewCheckListForId(this.checkListNumber)
      .subscribe((checkListDetail: CheckList) => {
        this.checkList = checkListDetail;
        if (isOnInit) {
          this.createForm();
        }
        this.isLoading = false;
      });
  }

  public updateCheckList() {
    this.submitted = true;
    this.formViewCheckList.markAsPristine();
    this.hasNoCheckListTask =
      this.checkList.task.filter(x => x.description !== '' && !x.isDeleted)
        .length === 0;

    if (!this.formViewCheckList.valid && this.hasNoCheckListTask) {
      this.isLoading = false;
      return;
    }

    if (!this.isUpdatingCheckList) {
      if (!this.formViewCheckList.valid) {
        this.submitted = true;
        return;
      }

      if (this.formViewCheckList.valid && !this.hasNoCheckListTask) {
        this.checkList.task = this.checkList.task.filter(
          (task: ITask) => !task.isDeleted
        );
        this.checkListsService
          .updateCheckListDetailForId(this.checkList)
          .subscribe(
            data => {
              this.viewCheckListForId(false);
              this.toastService.showToast(
                new Toast(
                  `Checklist ( ${data.numberWithPrefix} ) saved successfully`
                )
              );
            },
            (errorResponse: HttpErrorResponse) => {
              this.toastService.showErrorToast('Unable to save checklist');
              return;
            }
          );
      }
      this.showSaveButton = false;
    }
  }

  public isTaskAdded(): boolean {
    return this.checkList.task.every(task => task.isDeleted);
  }

  public handleTaskDescriptionChanges(event, task) {
    task.description = event.target.value;
    this.formViewCheckList.markAsDirty();
    this.showSaveButton = true;
    this.hasNoCheckListTask = false;
  }

  public handleAddCheckList() {
    this.addedTaskList = {
      sequenceNumber: this.checkList.task.length + 1,
      description: '',
      isDeleted: false
    };
    this.checkList.task.push(this.addedTaskList);
    this.formViewCheckList.markAsDirty();
    this.showSaveButton = true;
  }

  public handleDeleteItem(checkListIndex: number) {
    if (this.checkList && this.checkList.task) {
      this.checkList.task[checkListIndex].isDeleted = true;
      this.formViewCheckList.markAsDirty();
      this.showSaveButton = true;
    }
  }

  public handleCheckListNameInput(event) {
    const checklistValue = event.target.value;
    const filteredValue = checklistValue.replace(/[^a-zA-Z0-9 _\-(),.]/g, '');
    if (filteredValue !== checklistValue) {
      event.preventDefault();
      event.target.value = filteredValue;
      this.formViewCheckList.get('name').setValue(filteredValue);
    }
  }
}
