import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import {
  AuthenticationService,
  CheckListPermissions,
  PermissionGroup,
  Toast,
  ToastService
} from 'src/app/services';
import { CheckListsService } from 'src/app/services/check-lists/check-lists.service';
import { ITask } from 'src/app/services/check-lists/check-lists.types';
import { CheckListsWizard } from '../check-lists.wizard';

@Component({
  selector: 'app-create-check-list',
  templateUrl: './create-check-list.component.html',
  styleUrls: ['./create-check-list.component.scss']
})
export class CreateCheckListComponent implements OnInit {
  public isLoading = false;
  public formCreateCheckList: FormGroup;
  public submitAttempted = false;
  public tasks: ITask[] = [];
  public hasNoCheckListTask: Boolean;
  public canUserEditCheckList: boolean;

  constructor(
    private formBuilder: FormBuilder,
    private checkListsService: CheckListsService,
    private toastService: ToastService,
    private authenticationService: AuthenticationService,
    private checkListsWizard: CheckListsWizard,
    private router: Router
  ) {}

  public ngOnInit(): void {
    this.setPermissionBooleans();
    this.createCheckListForm();
  }

  public createCheckListForm() {
    const buildCreateCheckListForm = () => {
      this.formCreateCheckList = this.formBuilder.group({
        name: [
          '',
          Validators.compose([Validators.required, Validators.maxLength(50)])
        ]
      });
    };
    buildCreateCheckListForm();
    this.tasks = [{ sequenceNumber: 1, description: '', isDeleted: false }];
  }

  public createCheckList() {
    this.submitAttempted = true;
    this.isLoading = true;
    this.formCreateCheckList.markAsPristine();
    this.hasNoCheckListTask =
      this.tasks.filter(x => x.description !== '' && !x.isDeleted).length === 0;

    if (!this.formCreateCheckList.valid || this.hasNoCheckListTask) {
      this.isLoading = false;
      return;
    }

    if (this.formCreateCheckList.valid && !this.hasNoCheckListTask) {
      this.tasks = this.tasks.filter(x => x.description !== '' && !x.isDeleted);
      for (const [index, value] of this.tasks.entries()) {
        value.sequenceNumber = index + 1;
      }

      const name = this.formCreateCheckList.value.name;
      const task: ITask[] = [];

      this.tasks.forEach(x => {
        const newTask: ITask = {
          sequenceNumber: x.sequenceNumber,
          description: x.description
        };
        task.push(newTask);
      });

      this.checkListsService
        .createCheckList({
          name,
          task
        })
        .subscribe(data => {
          this.checkListsWizard.checkLists = data;
          this.toastService.showToast(
            new Toast(
              'Checklist (' + data.numberWithPrefix + ') saved successfully'
            )
          );

          if (this.canUserEditCheckList) {
            this.router.navigate(['check-list/edit-check-list']);
          } else {
            this.router.navigate(['check-list/view-check-list']);
          }
        });
    }
    this.isLoading = false;
  }

  public handleDeleteTask(task: ITask) {
    this.tasks.find(
      itemTask =>
        task.description === itemTask.description &&
        task.sequenceNumber === itemTask.sequenceNumber
    ).isDeleted = true;
    this.formCreateCheckList.markAsDirty();
  }

  public handleAddRow() {
    this.tasks = [
      ...this.tasks,
      { sequenceNumber: this.tasks.length + 1, description: '' }
    ];
    this.formCreateCheckList.markAsDirty();
  }

  public handleTaskInputKeyDown(event) {
    this.hasNoCheckListTask = false;
    this.formCreateCheckList.markAsDirty();
  }

  private setPermissionBooleans() {
    this.authenticationService
      .currentUserHasPermission({
        [PermissionGroup.CheckList]: [CheckListPermissions.CanEditCheckList]
      })
      .subscribe(hasPermission => {
        this.canUserEditCheckList = hasPermission;
      });
  }

  public handleChecklistNameInput(event) {
    const checklistValue = event.target.value;
    const filteredValue = checklistValue.replace(/[^a-zA-Z0-9 _\-(),.]/g, '');
    if (filteredValue !== checklistValue) {
      event.preventDefault();
      event.target.value = filteredValue;
      this.formCreateCheckList.get('name').setValue(filteredValue);
    }
  }
}
