import { NgModule } from '@angular/core';
import { CheckListsComponent } from './check-lists.component';
import { RouterModule, Routes } from '@angular/router';
import { CreateCheckListComponent } from './create-check-list/create-check-list.component';
import { ViewCheckListsComponent } from './view-check-lists/view-check-list.component';

export const routes: Routes = [
  {
    path: '',
    component: CheckListsComponent
  },
  {
    path: 'create-check-list',
    component: CreateCheckListComponent
  },
  {
    path: 'view-check-list',
    component: ViewCheckListsComponent
  },
  {
    path: 'edit-check-list',
    component: ViewCheckListsComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CheckListsRoutingModule {}
